.row {
	display: flex;
	/*flex-direction: row;*/
	/*flex-wrap: wrap;*/
	width: 100%;
}

.column {
	display: flex;
	flex-direction: column;
	flex-basis: 100%;
	flex: 1;
	border: 1px solid black;
	padding: 5px;
}

label {
	padding-right: 10px;
}

select {
	margin: 5px;
	width: 120px;
}

.row-umag {
	display: flex;
	flex-direction: row;
	/*flex-wrap: wrap;*/
	width: 100%;
}

.column-umag {
	display: flex;
	flex-direction: column;
	flex-basis: 100%;
	flex: 0.3;
	padding: 5px;
	width: 20%;
}

.column-tali {
	display: flex;
	flex-direction: column;
	flex-basis: 100%;
	flex: 1;
	padding: 5px;
	width: 100%;
}

.column-second {
	display: flex;
	flex-direction: column;
	flex-basis: 100%;
	flex: 1;
	width: 25%;
}

.padding-bot-umag {
	padding-bottom: 7px;
	border-bottom: 1px solid;
}

.bot-border {
	border-bottom: 1px solid;
}

.bot-border-fake {
	border-bottom: 1px transparent;
}

input {
	padding-bottom: 7px;
}

.color-light-grey {
	background-color: #C8C8C8;
}

.color-grey {
	background-color: #A9A9A9;
}
